<template>
  <div class="imgcode-container"  :style="imgCodeStyle">
    <p class="title">图形验证码</p>
    <slot></slot>
    <i class="close" @click="close"></i>
  </div>
</template>

<script>
export default {
  name: 'BaseAlertBox',
  props: {
    absTop: {
      type: Number,
      default: 0
    },
    absLeft: {
      type: Number,
      default: 0
    }
  },
  methods: {
    close () {
      this.$emit('hanleClose')
    }
  },
  computed: {
    imgCodeStyle () {
      return {
        top: (this.absTop / 100) + 'rem',
        left:(this.absLeft / 100) + 'rem',
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";
  .imgcode-container {
    position: absolute;
    z-index: 101;
    width:pxToRem(510px);
    height:pxToRem(286px);
    background: #fff;
    box-shadow: 0 pxToRem(12px) pxToRem(20px) 0 #B6BBBB;
    border-radius: pxToRem(4px);
    padding:pxToRem(30px);
    .title{
      font-size: pxToRem(30px);
      color: $color-text;
      font-weight: bold;
    }

    .close {
      position: absolute;
      top: pxToRem(20px);
      right: pxToRem(20px);
      width: pxToRem(44px);
      height: pxToRem(44px);
      background: url("../../assets/images/close.png");
      background-size: pxToRem(44px) pxToRem(44px);
    }
  }
</style>
